<script>
import AccountService from '@services/account'
import PatientService from '@services/patient'

export default {
  props: {
    banks: {
      type: Array,
      default: () => [],
    },

    detail: {
      required: false,
      type: Object,
      default: () => {
      },
    },
  },

  data () {
    return {
      refundForm: new window.Form({
        ledger_id: '',
        date: '',
        comments: '',
        header_id: '',
        source: '4',
        bank_account_id: '',
        details: [],
      }),

      bills: [],
    }
  },

  methods: {
    getPatientBills () {
      const vm = this

      PatientService.findPatientBills(this.detail.hashid)
        .then(({ data }) => {
          console.log(data)
          this.bills = data.bills
            .filter((bill) => bill.payment_id === vm.detail.id)
            .map((bill) => {
              return {
                id: bill.id,
                description: bill.description,
                total: bill.total,
                selected: false,
              }
            })
        }).catch()
    },

    setup () {
      this.refundForm.ledger_id = this.detail.invoice_id

      this.getPatientBills()
    },

    cleanUp () {
      this.bills = []

      this.refundForm.reset()
    },

    refundTransaction () {
      if (!this.bills.length) {
        this.$toast.error('Please select the bills')

        return
      }

      const selection = this.bills.find((bill) => bill.selected === true)

      if (!selection) {
        this.$toast.error('Please select the bills')

        return
      }

      this.refundForm.details = this.bills.filter((bill) => bill.selected === true)

      this.refundForm.post('/patient-billing/refund')
        .then(() => {
          this.$toast.success('Payment Refunded')

          this.$emit('bill:refunded')

          this.$bvModal.hide('refund-modal')
        })
        .catch(() => {
          this.refundForm.processing = false

          this.$toast.error('Something went wrong')
        })
    },

    toggleBulkReconcile () {
      const vm = this

      if (this.bills.length) {
        this.bills.forEach(function (transaction) {
          transaction.selected = vm.$refs.bulkBill.checked
        })
      }
    },
  },
}
</script>

<template>
  <div>
    <b-modal
      id="refund-modal"
      title="Refund Cash"
      title-class="font-18"
      no-close-on-backdrop
      no-close-on-esc
      :no-enforce-focus="true"
      hide-header-close
      content-class="bg-white rounded-3"
      body-class="px-4"
      modal-class="modal-blur"
      size="lg"
      footer-class="d-flex justify-content-between"
      @shown="setup"
      @hidden="cleanUp"
    >
      <div class="row gx-4">
        <div class="col-xl-6">
          <div class="form-group mb-4">
            <label for="" class="form-label">Date</label>
            <date-picker
                :max-date="new Date()"
              :value="refundForm.date"
              @on-change="(dateObj, dateStr) => {refundForm.date = dateStr}"
            />

            <span
              v-if="refundForm.hasError('date')"
              class="text-danger"
              v-text="refundForm.getError('date')"
            />
          </div>
        </div>
        <div class="col-xl-6">
          <div class="form-group mb-4">
            <label for="bank_account_id" class="form-label">Refund Method</label>
            <v-select
              id="bank_account_id"
              v-model="refundForm.bank_account_id"
              :options="banks"
              label="name"
              :reduce="(option) => option.id"
              :clearable="false"
            />
            <span
              v-if="refundForm.hasError('bank_account_id')"
              class="text-danger"
              v-text="refundForm.getError('bank_account_id')"
            />
          </div>
        </div>
      </div>

      <div class="form-group mb-4">
        <label for="comments" class="form-label">Reason</label>
        <textarea
          id="comments"
          v-model="refundForm.comments"
          required
          type="text"
          class="form-control"
          placeholder="Please leave a brief description detailing your reason for refunding"
        />

        <span
          v-if="refundForm.hasError('comments')"
          class="text-danger"
          v-text="refundForm.getError('comments')"
        />

        <span
          v-if="refundForm.hasError('ledger_id')"
          class="text-danger"
          v-text="refundForm.getError('ledger_id')"
        />
      </div>

      <div class="mb-3">
        <h1 class="h2 fw-bold mb-0 pb-0">
          Patient Bills
        </h1>
        <span class="text-muted">Please check the bills to fund</span> <br>
        <span class="text-danger small">selected bills will be deleted, Payment will be refunded</span>
      </div>

      <table class="styled-table table-hover">
        <thead>
          <tr>
            <th class="text-center" style="width: 5%">
              <input ref="bulkBill" type="checkbox" @input="toggleBulkReconcile">
            </th>
            <th class="ps-3" style="width: 85%">
              Description
            </th>
            <th class="pe-3 text-end" style="width: 15%">
              Amount
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(transaction, index) in bills" :key="index">
            <td class="text-center">
              <input v-model="bills[index].selected" type="checkbox">
            </td>
            <td class="ps-3">
              {{ bills[index].description }}
            </td>

            <td class="pe-3 text-end">
              {{ bills[index].total }}
            </td>
          </tr>
        </tbody>
      </table>

      <span
        v-if="refundForm.hasError('details')"
        class="text-danger"
        v-text="refundForm.getError('details')"
      />

        <div class="progress mb-2" v-if="refundForm.processing">
            <div class="progress">
                <div class="progress-bar progress-bar-indeterminate bg-green"></div>
            </div>
        </div>

      <template #modal-footer="{cancel}">
        <button
          class="btn btn-light"
          :disabled="refundForm.processing"
          @click="cancel"
        >
          Close
        </button>

        <button
          type="submit"
          class="btn btn-primary"
          :disabled="refundForm.processing"
          @click="refundTransaction"
        >
          Refund
        </button>
      </template>
    </b-modal>

    <b-modal
      id="refund-modal-form"
      title-class="font-18"
      no-close-on-backdrop
      no-close-on-esc
      :no-enforce-focus="true"
      content-class="rounded-lg"
      hide-header
    >
      <form id="refundForm" @submit.prevent="refundTransaction" />

      <template #modal-footer="{cancel}">
        <b-overlay :show="refundForm.processing" spinner-small>
          <button
            class="btn btn-light mr-3"
            :disabled="refundForm.processing"
            @click="cancel"
          >
            Close
          </button>

          <button
            class="btn btn-danger shadow-sm"
            form="refundForm"
            :disabled="refundForm.processing"
            @click="refundTransaction"
          >
            Refund
          </button>
        </b-overlay>
      </template>
    </b-modal>
  </div>
</template>

